              <img src="../../assets/Img/Icon/time.png" alt="" />
              {{videoInfo.time}}
            </span>
          </div>
        </div>
      </div>
      <!--视屏的主题-->
      <div class="videomain">
        <videoPlay :videoLink="videoInfo.link" :videoImg:="videoInfo.image"></videoPlay>


      </div>

      <div class="video-toolbar">
        <span class="like">
          <img
            src="../../assets/Img/Icon/dianzan.png"
