    padding: 10px 20px 10px 70px;
    .videotitle {
      padding-top: 12px;
      height: 85px;
      .title {
        margin-bottom: 12px;
        overflow: hidden;
        line-height: 24px;
        font-size: 20px;
        font-weight: 500;
        color: #18191c;
      }
      .video-data {
        display: flex;
        position: relative;
        overflow: hidden;
        height: 24px;
        line-height: 20px;
        align-items: center;
        font-size: 12px;
        color: #9499a0;
        .video-data-list {
          display: flex;
          align-items: center;
          overflow: hidden;
          box-sizing: border-box;
          .videoItem {
            margin-right: 12px;
            display: inline-flex;
            text-align: center;
            overflow: hidden;
          }
          img {
            margin-right: 4px;
            width: 20px;
            height: 20px;
          }
        }
      }
    }
    .videomain {
      width: 100%;
    }
    .video-toolbar {
      padding-top: 16px;
      padding-bottom: 12px;
      height: 50px;
      line-height: 25px;
      border-bottom: 1px solid #e3e5e7;
      color: #61666d;
      .like,.share,.collection {
        display: flex;
        align-items: center;
        margin-right: 45px;
      }
      .icon {
        margin-top: -6px;
        margin-right: 8px;
        width: 24px;
        height: 24px;
      }
      .test-info {
        overflow: hidden;
        text-overflow: ellipsis;
        word-break: break-word;
        white-space: nowrap;
      }
    }
    .videoinfo {
      margin: 16px 0;
      min-height: 30px;
      .infoconnect {
        white-space: pre-line;
        font-size: 15px;
        color: #18191c;
        letter-spacing: 0;
        line-height: 24px;
        overflow: hidden;
      }
    }
    .videotype {
      margin: 16px 0 10px 0;
      padding-bottom: 12px;
      height: 46px;
      border-bottom: 1px solid #e3e5e7;
      .tag {
        float: left;
        margin: 0 12px 8px 0;
        .taginfo {
          padding: 0 12px;
          height: 28px;
          line-height: 28px;
          font-size: 14px;
          border-radius: 100px;
          color: #61666d;
          background: #f1f2f3;
        }
      }
    }
    .videocommit {
      margin-top: 24px;
      .reply-notecion {
        margin-bottom: 22px;
        .reply-bar {
          display: flex;
          align-items: center;
          .nav-item {
            font-size: 20px;
            .total-reply {
              margin: 0 36px 0 6px;
              color: #9499a0;
              font-size: 14px;
            }
          }
        }
      }
      .reply-List{
        .children-List{
          margin-left: 6%;
          width: 100%;
        }
        .children-Commit{
          margin: 10px 0;
          margin-left: 5%;
          width: 95%;
        }
        .totalNumber{
          display: flex;
          margin-left: 10%;
          color: #9499a0;
          font-size: 12px;
          .total{
            margin-right: 10px;
          }
          .getMore:hover{
            color: #00aeec;
          }
        }

        //显示子评论的宽度 
        .hidden{
          height: 90px;
          overflow: hidden;
          transition: 3;
        }

        //将没有子评论的相关信息隐藏 
        .hiddenBox{
          height: 0px;
          overflow: hidden;
        }
        //显示相关信息 
        .showBox{
          height: 100%;
          overflow:auto;
        }
      }
    }
  }
  .MainBox-right {
    padding: 0 25px;
    width: 400px;
    .videoList{
      .item-box{
        display: flex;
