            align-content: center;
            margin-top: 5px;
            .total-left,.total-right{
              display: flex;
              margin: 0 5px;
              // margin-left: 0px;
              height: 16px;
              .left-img,.right-img{
                margin-right: 4px;
                width: 17px;
                height: 17px;
